body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.chat-container
{
  border:5px solid #42a5f5;  
   margin-top:10px; 
   padding: 10px;
}
.nav {
	background-size: cover;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-position: top;
	text-align: center;
	height: auto;
}

.navlogo {
	align-content: center;
	width: 80%;
	height: auto;
	align-content: center;
	padding-top: 40px;
	padding-bottom: 20px;
}

#classComponent {
	padding: 40px;
	color: #ffffff;
	border: #ffffcc;
	border-width: 5px;
}

.teachername {
	color: white;
	font-size: 36px;
}

#classComponent {
	padding: 20px;
}

#studentCollection {
	background: #ffffcc;
}

.studentComponent {
	font-size: 2rem;
}

i.icon-creamyyy {
	color: #ffffcc;
}

.studentname {
	color: white;
	font-size: 18px;
}

.studentheader {
	color: white;
}

.validate {
    color: white;
}
@media (max-width: 1100px) {
    #squarelogo {
        width: 70%;
        height: auto;
        align-content: center;
        padding-top: 40px;
    }
}

@media (min-width: 1101px) {
    #squarelogo {
        width: 30%;
        height: auto;
        align-content: center;
        padding-top: 60px;
    }
}

html,
body {
	background-image: url("/blackboardbg.jpg");
	background-position: center;
	background-size: cover;
	background-color: #336633;
}

.contentdiv {
	min-height: calc(100vh - 50px);
}

